<template>
  <div class="container">
    <transition name="van-slide-right">
      <router-view></router-view>
    </transition>
    <!-- tab 底部组件 -->
    <Vtabbar></Vtabbar>
  </div>
</template>
<script>
/**引入组件 */
import Vtabbar from "./tabber.vue";
export default {
  data() {
    return {};
  },
  components: {
    Vtabbar
  }
};
</script>




