<template>
  <div class="tab">
    <van-tabbar v-model="active">
      <van-tabbar-item name="Index" icon="home-o" to="/Index">游戏</van-tabbar-item>
      <van-tabbar-item name="Gift" icon="search" to="/Gift">礼包</van-tabbar-item>
      <van-tabbar-item name="Games" icon="friends-o" to="/Games">赛事游戏</van-tabbar-item>
      <van-tabbar-item name="Shoplist" icon="setting-o" to="/Shoplist">商品</van-tabbar-item>
      <van-tabbar-item name="My" icon="setting-o" to="/My">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: ""
    };
  },
  created() {
    /**根据当前路由选中tab高亮 */
    this.active = this.$route.path.replace(/[/]/g, "");
  }
};
</script>

<style lang="">
.van-tabbar {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 98px;
  background-color: #fff;
}
.van-tabbar-item__icon {
  position: relative;
  margin-bottom: 0.15625rem;
  font-size: 36px;
}
.van-tabbar-item--active {
    color: #1989fa;
}
.van-tabbar-item {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    font-size: 22px;
    line-height: 1;
}
</style>


